<template lang="pug">
	.skills-holder
		.img-bg
		.overlay
		.skillset-holder
			p.header Skills
			.skills-bars
				.skill-row('v-for'="skill in skills")
					.skill-name {{skill.name}}
					.skill-percentage
						.perc-bar(':style'="{width: skill.level + '%'}")
						.perc-number {{skill.level}}%
</template>
<style lang="scss" scoped>
.skills-holder {
	width: 100%;
	height: 115vh;
	position: relative;
	.img-bg {
		width: 100%;
		height: 100%;
		object-fit: cover;
		background: url("../assets/p2.webp") no-repeat center top;
		background-attachment: fixed;
		background-size: cover;
	}
	.overlay {
		width: 100%;
		height: 100%;
		top: 0;
		position: absolute;
		background: rgba(51, 59, 80, 0.9);
	}
	.skillset-holder {
		width: 50%;
		position: absolute;
		top: 40%;
		left: 25%;
		@media screen and (max-width: 662px) {
			left: 0;
			width: 100%;
		}
		p.header {
			font-size: 45px;
			text-align: center;
			color: #fff;
			margin-bottom: 60px;
		}
		.skills-bars {
			margin-left: -10%;
			.skill-row {
				display: flex;
				align-items: center;
				margin-bottom: 30px;
				.skill-name {
					text-align: right;
					flex: 0 0 200px;
					color: #fff;
					margin-right: 20px;
					text-transform: uppercase;

					@media screen and (max-width: 662px) {
						flex-basis: 35%;
					}
				}
				.skill-percentage {
					flex: 1 1 auto;
					display: flex;
					align-items: center;

					@media screen and (max-width: 662px) {
						flex: 0 0 42%;
					}

					.perc-bar {
						background: #1abc9c;
						height: 10px;
						flex-grow: 0;
						flex-shrink: 0;
					}
					.perc-number {
						color: #fff;
						margin-left: 20px;
						font-size: 14px;
						flex: 0 0 50px;
					}
				}
			}
		}
	}
}
</style>
<script>
export default {
	name: 'skills',
	data() {
		return {
			skills: [
				{
					name: 'Angular 4',
					level: 90
				},
				{
					name: 'React',
					level: 95
				},
				{
					name: 'Redux',
					level: 100
				},
				{
					name: 'SASS',
					level: 80
				},
				{
					name: 'RxJS',
					level: 75
				},
				{
					name: 'TypeScript',
					level: 85
				},
				{
					name: 'HTML 5, CSS 3',
					level: 90
				}
			]
		}
	}
}
</script>




