<template lang="pug">
	.cert-holder
		p.header Certifications
		#carousel-content.owl-carousel.owl-theme(ref="carousel")
			img.img-content('v-for'="cert of certs" :src="'/static/img/projects/' + cert.photo", alt='img')
</template>

<style lang="scss">
.cert-holder {

	width: 100%;
	padding: 100px 0;
	height: auto;
	position: relative; // background: rgba(51, 59, 80, 1);	
	background: url("../assets/bridge.jpg") no-repeat center bottom;
	background-attachment: fixed;
	background-size: cover;

	p.header {
		font-size: 45px;
		text-align: center;
		color: #fff;
		margin-bottom: 60px;
	}

	#carousel-content {
		width: 70%;
		margin: 100px auto;
		margin-top: 50px;
		.owl-nav {
			.owl-prev,
			.owl-next {
				border-radius: 50%;
				width: 25px;
				height: 25px;
			}
		}
		img.img-content {
			max-height: 70vh;
			max-width: 100%;
			margin: auto;
			object-fit: contain;
		}
	}
}
</style>

<script>
export default {
	name: 'certifications',
	mounted() {
		$(this.$refs.carousel).owlCarousel({
			loop: true,
			margin: 10,
			// nav: true,
			autoplayTimeout: 2000,
			autoplay: true,
			items: 1,
			navText: ['&#10094;', '&#10095;',]
		});
	},
	data() {
		return {
			certs: [
				{
					name: 'Udacity',
					photo: 'cert/udacity.jpg'
				},
				{
					name: 'Microsoft',
					photo: 'cert/microsoft.png'
				},
				{
					name: 'Oracle',
					photo: 'cert/oracle.png'
				},
			]
		}
	}
}
</script>




